import React, { useState } from "react";
import { useHistory } from "react-router-dom";
import EMAIL_ICON from "../images/icons/icon_email.png";
import LOCK_ICON from "../images/icons/icon_lock.png";
import PROFILE_ICON from "../images/icons/icon_profile.png";
import { PATH } from "../constants";
import { doLogin, doSignup } from "../api";

export function SignupPage() {
  const history = useHistory();
  const [title, setTitle] = useState("");

  const [name, setName] = useState("");
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");

  function handleSignup() {
    const request: SignupRequest = {
      name,
      email,
      password: password,
    };

    doSignup(request)
      .catch(() => {
        setTitle("  ");
      })
      .then(() =>
        doLogin({
          email: request.email,
          password: request.password,
        }).then(() => {
          history.push(PATH.MAIN);
        })
      );
  }

  return (
    <div className="login-page page">
      <div className="content">
        <h2>{title}</h2>
        <form>
          <img className="icon" src={PROFILE_ICON}></img>
          <input
            type="text"
            placeholder="User Name"
            value={name}
            onChange={(ev) => {
              setName(ev.target.value);
            }}
          ></input>

          <img className="icon" src={EMAIL_ICON}></img>
          <input
            type="email"
            placeholder="example@gmail.com"
            value={email}
            onChange={(ev) => {
              setEmail(ev.target.value);
            }}
          ></input>

          <img className="icon" src={LOCK_ICON}></img>
          <input
            type="password"
            placeholder="pasword here"
            value={password}
            onChange={(ev) => {
              setPassword(ev.target.value);
            }}
          ></input>
        </form>
        <button onClick={handleSignup}></button>
        <p className="signup-form">
            ?
          <button
            onClick={() => {
              history.push(PATH.LOGIN);
            }}
          >
            
          </button>
        </p>
      </div>
    </div>
  );
}
