import React from "react";
import { SavedPlace as SavedPlace } from "./SavedPlace";

export function RecommendPlaces(props: { places: UserPlace[] }) {
  const { places } = props;
  console.log(places);
  return (
    <div className="recommend-places">
      <h3 className="title">
        {places.length > 0 ? "    " : "  ."}
      </h3>
      {places.map((place) => (
        <SavedPlace key={Math.random()} place={place} />
      ))}
    </div>
  );
}
