import React from "react";
import ReactDOM from "react-dom";
import PIN_ICON from "../images/icon_pin.png";
import PIN_ICON_BLUE from "../images/icon_pin_blue.png";
import "./marker.css";

export function SavedMarkerIcon(content: string): HtmlIcon {
  const element = (
    <>
      {content && <label className="title">{content}</label>}
      <img className="icon" src={PIN_ICON} />
    </>
  );
  const root = document.createElement("div");
  root.className = "marker";
  ReactDOM.render(element, root);
  return {
    content: root,
  };
}

export function DefaultMarkerIcon(address?: string): HtmlIcon {
  const element = (
    <>
      {address && <label className="address">{address}</label>}
      <img className="icon" src={PIN_ICON_BLUE} />
    </>
  );
  const root = document.createElement("div");
  root.className = "marker";
  ReactDOM.render(element, root);
  return {
    content: root,
  };
}
