import { DefaultMarkerIcon, SavedMarkerIcon } from "./marker-icons";

export function setMarker(args: {
  title: string;
  address?: string;
  position: Coord;
  type: MarkerType;
  map: naver.maps.Map;
}): naver.maps.Marker {
  const { title, position, address, map, type } = args;
  const options: naver.maps.MarkerOptions = {
    map,
    position,
  };
  switch (type) {
    case "SAVED_PLACE":
      options.icon = SavedMarkerIcon(title);
      break;
    case "DEFAULT":
      options.icon = DefaultMarkerIcon(address);
      break;
  }
  const marker = new naver.maps.Marker(options);
  return marker;
}
