import React, { useState } from "react";
import { useEffect, useRef } from "react";
import { setMarker } from "./overlay";

interface NaverMapProps {
  $elementId?: string;
  clientId: string;
  options: NaverMapOptions;
  // onInitialized?: (map: naver.maps.Map) => void;
}

export interface NaverMapOptions {
  width?: string;
  height?: string;
  center?: naver.maps.LatLng;
  zoom?: number;
  markers?: MarkerProps[];
}

export function NaverMap(props: NaverMapProps) {
  const { $elementId, clientId } = props;
  const options = props.options;

  const { width, height, center, zoom, markers } = props.options;
  useEffect(() => {
    console.log(`option changed`);
    console.log(width);
  }, [options]);
  const naverMapScriptUrl = `https://openapi.map.naver.com/openapi/v3/maps.js?ncpClientId=${clientId}`;
  const $mapRef = useRef<HTMLDivElement | null>(null);
  const [map, setMap] = useState<naver.maps.Map>();
  const prevMarkers = useRef<naver.maps.Marker[]>();
  useEffect(() => {
    if (!map) {
      loadScript(naverMapScriptUrl, initialMap);
    }
  }, []);
  useEffect(() => {
    if (map && center) {
      map.setCenter(center);
    }
    if (map && zoom) {
      map.setZoom(zoom, true);
    }
    if (map && markers && markers.length > 0) {
      prevMarkers.current?.forEach((marker) => marker.setMap(null));
      drawMarkers(map, markers);
    }
  }, [center, zoom, markers]);

  function drawMarkers(map: naver.maps.Map, markers: MarkerProps[]) {
    if (markers && markers.length > 0) {
      const markerList = markers.map((marker) => {
        const { title, address, position, type } = marker;
        return setMarker({
          title,
          address,
          map,
          position,
          type,
        });
      });
      prevMarkers.current = markerList;
    }
  }

  function initialMap() {
    if (!$mapRef.current) {
      throw new Error("cannot get map element");
    }

    const mapOptions: naver.maps.MapOptions = {
      zoom: 11,
    };

    const map = new naver.maps.Map($mapRef.current, mapOptions);
    setMap(map);
  }

  return (
    <div id={$elementId} className="naver-map-container">
      <div
        id="map"
        ref={$mapRef}
        style={{
          width: `${width ? width : "100%"}`,
          height: `${height ? height : "100%"}`,
        }}
      ></div>
    </div>
  );
}

function loadScript(url: string, callBack: () => void) {
  const script = document.createElement("script");
  script.type = "text/javascript";
  script.src = url;
  script.onload = callBack;
  document.body.appendChild(script);
}
