import React from "react";
import { getDateString } from "../../modules/document-module";

export function PlaceReview(props: {
  place: SavedPlace;
  onEditCallback: () => void;
}) {
  const { place } = props;
  return (
    <div className="place-review">
      <p className="create-time">{getDateString(place.create_datetime)} </p>
      <div className="rate">
        {new Array(place.rate).fill("").map(() => (
          <p key={Math.random()}></p>
        ))}
      </div>
      {place.description ? (
        <p className="description">{place.description}</p>
      ) : (
        <p className="description disabled">  .</p>
      )}
      <button className="edit-button" onClick={props.onEditCallback}>
        
      </button>
    </div>
  );
}

